@import url('https://fonts.googleapis.com/css2?family=Patua+One&display=swap');
*
{
  margin:0;
  padding:0;
}
body
{
 background-image: url(https://www.imageshine.in/uploads/sub_category/Pin-and-White-wave-background-wallpaper-free-download.jpg);
 font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
 display: flex;
 justify-content: center;
 align-items: center;
 min-height: 100vh;
}
.header{
  text-align: center;
  color:blue;
}
.app{
  background:linear-gradient(118deg,#00ff2b 0%, #1b00ff 80%);
  width: 95%;
  min-height: 200px;
  height: min-content;
  border-radius: 15px;
  padding: 20px;
  box-shadow:10px 10px 42px 0px rgba(194, 15, 15, 0.75) ;
  display: flex;
  justify-content: space-evenly;
}
.score-section
{
 display: flex;
 align-items: center;
 font-size: 25px;
 color:white;
 font-weight: bold;
 }
 .score-section:hover
 {
  background: #38bfc4;

 }
.question-section
{
  width:100%;
  position: relative;
  }
.question-count
{
  margin-bottom: 20px;
  font-size: 20px;
  color: #2c052a;
}
.question-count span
{
 font-size: 30px;
 color: #2c052a;
}
.question-text
{
 color:white;
 font-weight: bold;
 font-size: 20px;
}
.answer-section
{
  display: flex;
  flex-direction: column;
  width:100%;
  justify-content: space-between;
}
button
{
  width:100%;
  font-size: 16px;
  color:#ffffff;
  background: linear-gradient(42deg,#ff00f3 0%, #4bff00 100%);
  border-radius:15px;
  display:flex;
  padding: 5px;
  justify-content: flex-start;
  align-items: center;
  border:3px solid blanchedalmond;
  cursor:pointer;
}
button:hover
{
  background: #ff8c00;
}